/* eslint-disable multiline-ternary */
import {
  Fragment,
  useContext,
  useEffect,
  useState,
  lazy,
  Suspense
} from "react"
import { useParams, Link, useNavigate } from "react-router-dom"
import toast from "react-hot-toast"
import Cleave from "cleave.js/react"
import {
  Row,
  Col,
  Alert,
  Card,
  CardBody,
  Form,
  Button,
  Input,
  Label,
  Modal,
  ModalBody,
  ModalHeader,
  CardText,
  CardHeader,
  CardTitle,
  Nav,
  NavItem,
  NavLink,
  TabContent,
  TabPane
} from "reactstrap"
import { useTranslation } from "react-i18next"
import UILoader from "@components/ui-loader"
import Spinner from "@components/spinner/Loading-spinner"
import "moment/locale/id"
import { ThemeColors } from "@src/utility/context/ThemeColors"
import { selectThemeColors } from "@utils"
import Select from "react-select"
import Flatpickr from "react-flatpickr"
import "@styles/react/libs/flatpickr/flatpickr.scss"
import { Indonesian } from "flatpickr/dist/l10n/id.js"
import { useForm, Controller } from "react-hook-form"
import { NoDataDisplay, Loader } from "@src/shared/CommonComponents"
import {
  storeName,
  pageTitle,
  postData,
  putData,
  resetSelectedData,
  getDataById,
  extraCrud
} from "../store"
import { useDispatch, useSelector } from "react-redux"
import FormAddEdit from "../Form"
import { MenuOpen, Link as LinkIcon } from "@mui/icons-material"

const DetailPage = () => {
  const { t } = useTranslation()
  const { id, pageType } = useParams()
  const dispatch = useDispatch()
  const store = useSelector((state) => state[storeName])

  const { colors } = useContext(ThemeColors)
  const [loadingText, setLoadingText] = useState("Please Wait")
  const navigate = useNavigate()
  const [active, setActive] = useState("1")
  const toggleTab = (tab) => {
    if (active !== tab) {
      setActive(tab)
    }
  }

  const allStore = useSelector((state) => state)
  const [dynamicComponents, setDynamicComponents] = useState({})

  function sortedObject(unordered) {
    return Object.keys(unordered)
      .sort()
      .reduce((obj, key) => {
        obj[key] = unordered[key]
        return obj
      }, {})
  }

  const countChanges = (data) => {
    if (pageType === "edit") {
      const storeData = { ...sortedObject(store.selectedData) }
      delete storeData["id"]
      for (let key in storeData) {
        if (!(key in data)) delete storeData[key]
      }
      // console.log(JSON.stringify(sortedObject(storeData)))
      // console.log(JSON.stringify(sortedObject(data)))
      return (
        JSON.stringify(sortedObject(storeData)) !==
        JSON.stringify(sortedObject(data))
      )
    } else {
      return true
    }
  }

  const updateDataToApi = (data) => {
    if (countChanges(data)) {
      if (
        Object.values(data).every((field) => {
          return field !== null && field !== ""
        })
      ) {
        setLoadingText(`${t("Updating")} ${t(pageTitle)} ${t("Detail")} Data`)
        dispatch(putData(data)).then((res) => {
          // console.log(res)
          if (res.meta.requestStatus === "fulfilled") {
            navigate(-1)
            toast.success(`${t("Successfully Updated")} ${t(pageTitle)}`, {
              style: {
                padding: "16px",
                color: colors.success.main,
                border: `1px solid ${colors.success.main}`
              },
              iconTheme: {
                primary: colors.success.main
              }
            })
          } else if (res.meta.requestStatus === "rejected") {
            toast.error(res.payload.message, {
              style: {
                padding: "16px",
                color: colors.danger.main,
                border: `1px solid ${colors.danger.main}`
              },
              iconTheme: {
                primary: colors.danger.main
              }
            })
          }
        })
      } else {
        for (const key in data) {
          if (data[key]?.length === 0) {
            setError(key, {
              type: "manual"
            })
          }
        }
      }
    } else {
      toast(`${t("You haven't changed anything")}`, { icon: "ðŸ‘" })
    }
  }

  const postDataToApi = (data) => {
    if (
      Object.values(data).every((field) => {
        return field !== null && field !== ""
      })
    ) {
      setLoadingText(`${t(`Creating a New ${pageTitle}`)}`)
      dispatch(postData(data)).then((res) => {
        // console.log(res)
        if (res.meta.requestStatus === "fulfilled") {
          navigate(-1)
          toast.success(`${t("Successfully Created")} ${t(pageTitle)}`, {
            style: {
              padding: "16px",
              color: colors.success.main,
              border: `1px solid ${colors.success.main}`
            },
            iconTheme: {
              primary: colors.success.main
            }
          })
        } else if (res.meta.requestStatus === "rejected") {
          toast.error(res.payload.message, {
            style: {
              padding: "16px",
              color: colors.danger.main,
              border: `1px solid ${colors.danger.main}`
            },
            iconTheme: {
              primary: colors.danger.main
            }
          })
        }
      })
    } else {
      for (const key in data) {
        if (data[key] !== null && data[key]?.length === 0) {
          setError(key, {
            type: "manual"
          })
        }
      }
    }
  }

  function handleReset() {
    dispatch(resetSelectedData())
    // setpageType("add")
    return navigate(-1)
  }

  useEffect(() => {
    // console.log(pageType)
    // if (!store.isLoadingDetail) {
    // console.log(store.selectedData?.id !== parseInt(id))
    if (
      pageType === "edit" &&
      !store.isLoadingDetail &&
      (!store.selectedData || store.selectedData?.id !== parseInt(id))
    ) {
      // setLoadingText(
      //   `${t("Loading")} ${t("Detail")} ${t("Data")} ${t(pageTitle)}`
      // )
      // setLoading(true)
      dispatch(getDataById(id)).then((res) => {
        if (res.meta.requestStatus === "rejected") {
          // setLoading(false)
          toast.error(res.payload.message, {
            style: {
              padding: "16px",
              color: colors.danger.main,
              border: `1px solid ${colors.danger.main}`
            },
            iconTheme: {
              primary: colors.danger.main
            }
          })
        } else {
          // setLoading(false)
          // setShowModal(true)
        }
      })

      // DYNAMIC IMPORT COMPONENTS
      let componentsImport = {}
      const dynamicFormRelation = extraCrud
      for (let i = 0; i < dynamicFormRelation.length; i++) {
        const crudName = dynamicFormRelation[i]["crudName"]
        const crudLabel = dynamicFormRelation[i]["crudLabel"]
        const path_name = dynamicFormRelation[i]["crudName"]

        ////////////OTHERS CRUD
        const MyComponent = lazy(() => import(`../../${path_name}`))
        componentsImport[`${path_name}_comp`] = (
          <Fragment>
            <MyComponent
              key={`extraCrud_${crudName}`}
              lookupMode={true}
              lookupMode_defaultFilter={{
                [`filter_${storeName.replaceAll("apps_", "id_")}`]:
                  parseInt(id)
              }}
              lookupMode_fieldName={crudName}
              lookupMode_dropdown={false}
              lookupMode_isMulti={null}
              lookupMode_onChange={null}
              lookupMode_defaultValue={null}
              lookupMode_isM2MDefValFromGrid={null}
            />
          </Fragment>
        )
      }

      setDynamicComponents(componentsImport)
    }
    // }
  }, [dispatch, store.selectedData])

  return pageType === "edit" ? (
    !store.isLoadingDetail ? (
      store.selectedData !== null && store.selectedData !== undefined ? (
        <div className="app-user-view">
          <Row>
            <Col xl="5" lg="6" xs={{ order: 1 }} md={{ order: 0, size: 6 }}>
              <Card>
                <CardHeader>
                  <h2>
                    {t("Edit")} {t(pageTitle)}
                  </h2>
                </CardHeader>
                <CardBody>
                  <FormAddEdit
                    formState="edit"
                    updateDataToApi={updateDataToApi}
                    postDataToApi={postDataToApi}
                    handleReset={handleReset}
                  />
                </CardBody>
              </Card>
            </Col>
            <Col xl="7" lg="7" xs={{ order: 0 }} md={{ order: 1, size: 7 }}>
              <Nav pills className="mb-2">
                {extraCrud.map((item, index) => {
                  const path_name = item.crudName
                  return (
                    <NavItem key={`navItem_${index + 1}`}>
                      <NavLink
                        active={active === `${index + 1}`}
                        onClick={() => toggleTab(`${index + 1}`)}
                      >
                        <MenuOpen className="font-medium-3 me-50" />
                        <span className="fw-bold">{t(item.crudLabel)}</span>
                      </NavLink>
                    </NavItem>
                  )
                })}
              </Nav>
              <TabContent activeTab={active}>
                {extraCrud.map((item, index) => {
                  const path_name = item.crudName
                  return (
                    <TabPane
                      key={`tabPane_${index + 1}`}
                      tabId={`${index + 1}`}
                    >
                      <Card>
                        {item.crudDescription && (
                          <CardHeader>
                            <CardTitle tag="h4">
                              {t(item.crudDescription)}
                            </CardTitle>
                          </CardHeader>
                        )}
                        <CardBody className="pt-1">
                          {dynamicComponents[`${path_name}_comp`]}
                        </CardBody>
                      </Card>
                    </TabPane>
                  )
                })}
              </TabContent>
            </Col>
          </Row>
        </div>
      ) : (
        <Alert color="danger">
          <h4 className="alert-heading">{t("User not found")}</h4>
          <div className="alert-body">
            {t("User with id")}: {id}{" "}
            {t("doesn't exist. Check list of all Users")}:{" "}
            <Link to="/apps/user/list">{t("Users List")}</Link>
          </div>
        </Alert>
      )
    ) : (
      <Fragment>
        <div className="app-user-view">
          <UILoader
            className="mt-5"
            blocking={store.isLoadingDetail}
            loader={<Loader />}
          ></UILoader>
        </div>
      </Fragment>
    )
  ) : (
    <div className="app-user-view">
      <Row>
        <Col xl="12" lg="12" xs={{ order: 1 }} md={{ order: 0, size: 5 }}>
          <Card>
            <CardHeader>
              <h2>
                {t("Add")} {t(pageTitle)}
              </h2>
            </CardHeader>
            <CardBody>
              <FormAddEdit
                formState="add"
                updateDataToApi={updateDataToApi}
                postDataToApi={postDataToApi}
                handleReset={handleReset}
              />
            </CardBody>
          </Card>
        </Col>
      </Row>
    </div>
  )
}
export default DetailPage
